.button {
    width: 132 px;
    height: 30px;
    padding: 5px 24px 5px 24px;
    border: none;
    border-radius: 3px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    text-align: left;
}

.disabled {
    background-color: #004D99;
    color: #002E5C;
}

.secondary {
    border: 2px solid  #0066CC;
    color: #0066CC;
}

.default {
    background-color: #0066CC;
    color: white;
}

.red {
    background-color: red;
    color: white;
}

.default:hover {
    background-color: #0080FF;

}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;

}

.secondary:active {
    background-color: #C9E5FF;

}

.red:active {
    background-color: #B21232;

}

.button:disabled {
}
